//TRIGGER HEIGHT DINAMICALLY
var startingTriggerTop = 100
$('.trigger').each(function(i) {
    if(i==0){
        $(this).css('top', '0vh');
    }else{
        $(this).css('top', (startingTriggerTop * (i) ) + 'vh');
    }
});





